#home{
    height: 100vh;
    background-image: url('../../images/snowy-mountains.jpg');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
    display: flex;
    overflow-y: scroll;
    
}

.margin-from-nav
{
    margin-left: 20%;
}

#dashboard-content{
    width: 80%;
    height: 100%;
    display: flex;
}

#db-title{
    /* background: rgba(0, 77, 91, 0.57); */
    background: rgba(190, 188, 0, 0.78);
    width: 80%;
    align-self: center;
    text-align: center;
    padding: 2rem;
}

#home h1{
    font-size: 2.5rem;
    font-weight: 500;
    color: white;
}

@media only screen and (max-width: 800px){
    #db-title, #dashboard-content{
        width: 100%;
    }
}