#contact{
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    border-radius: 2px;
    flex-wrap: wrap;
    margin-bottom: 5rem;
    margin-top: 5rem;
}

#contact form{

    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    row-gap: 1rem;
    width: 40%;
    margin: 0.2rem;
    padding: 1rem;
    border-left: 1px solid #d9d9d9;
    
}

#contact input{
    height: 2rem;
}

#contact input, #contact textarea{
    border: 1px #0000001f solid;
}

#contact input, #contact textarea, #contact button{
    width: 80%;    
    font-size: 1rem;
    font-family:  sans-serif;
    border-radius: 2px;
    margin: 1rem 0;
    color: #4d4d4d;
}

#contact textarea{
    height: 200px;
}

#contact button{
    background: rgb(0, 105, 119);
    border: none;
    color: white;
    padding: 0.5rem;
}

#contact h2{
    background-color: rgba(240, 240, 240, 0.85);
    width: 80%;
    padding: 1rem;
    margin-left: 1rem;
    color: rgb(0, 77, 91);
    font-size: 1.5rem;
    display: block;
}

#contact p{
    background: rgb(251, 251, 251);
    width: 40%;
    text-align: center;
    padding: 1rem;
    line-height: 1.5;
    font-size: 1rem;
    color: #4f4f4f;
    border-radius: 2px;
}

@media only screen and (max-width: 800px){
    #contact p{
        width: 90%;
        order: 3;
    }

    #contact form{
        width: 90%
    }
}