#navbar, #mobile-nav{
    width: 20%;
    display: flex;
    background: #ffffff78;
    height: 100vh;
    position: fixed;
    flex-wrap: wrap;
    align-content: flex-start;
    row-gap: 1rem;
}

#mobile-nav{
    display: none;
}

#navbar-links{
    width: 60%;
    display: flex;
    margin: 0 auto;
    flex-wrap: wrap;
}

#navbar a, #mobile-nav a{
    text-decoration: none;
    color: #424242;
    margin: 0.5rem 1rem;
    width: 100%;
    text-align: right;
    transition: font-size 1s, color 1s;
}

#navbar a:hover{
    font-weight: 600;
    color: rgb(63, 63, 63);
    font-size: 1.1rem;
    text-decoration: underline;
}

#db-logo{
    display: block;
    width: 60%;
    height: auto;
    margin: 1rem auto;
}

.nav-social-logos{
    margin: 0 !important;
    width: 20% !important;
}

.nav-social{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    margin: 0.5rem 1rem;
}

@media only screen and (max-width: 800px){
    #navbar, #mobile-nav{
        width: 100%;
        max-height: 10vh;
        margin-left: 0;
        z-index: 2;
        align-items: center;
        overflow: hidden;
        flex-wrap: nowrap;

    }

    #navbar{
        display: none;
    }

    #mobile-nav{
        display: flex;
        justify-content: space-evenly;
    }
    
    #db-logo{
        max-width: 5%;
        height: auto;   
        margin: 10px;     
    }

    #navbar-links{
        flex-wrap: nowrap;
        width: 90%;
        margin: 0;
        overflow: hidden;        
    }

    .margin-from-nav
    {
        margin: 0px;
    }

    #mobile-nav a{
        text-align: center;
    }

}