.display-person{
    width: 60%;
    padding: 5rem 0;
    background: rgba(239, 239, 239, 0.23);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    row-gap: 2rem;
}

.display-person img{
    width: 20%;
    height: auto;
    box-shadow: 10px 10px 20px black;
    border-radius: 2px;
    border: 5px solid white;
}

.display-person h3{
    background-color: black;
    width: 80%;
    padding: 1rem;
    margin-left: 1rem;
    color: white;
    font-size: 1.2rem;
    font-weight: 200;
}

.display-person p{
    width: 45%;
    font-size: 1rem;
    text-align: justify;
    line-height: 2;
    background: white;
    padding: 2rem;
    border-radius: 2px;
    font-size: 0.8rem;
}

.email{
    background: rgb(153, 153, 153);
    width: 50%;
    text-align: center;
    font-size: 1rem;
    padding: 0.5rem 0;
    border-radius: 2px;
    color: white;
    text-decoration: none;
}

@media only screen and (max-width: 1000px){
    .display-person{
        width: 90%;
    }

    .display-person img{
        width: 60%;
    }

    .email, .display-person p{
        width: 90%;
    }
}