#what-are-we-info{
    display: flex;
    justify-content: space-evenly;
    align-content: center;
    align-items: center;
    min-height: 100vh;
    width: 80%;
    flex-wrap: wrap;
    row-gap: 5rem;
}

#what-are-we p{
    text-align: center;
    line-height: 2;
    padding: 1rem;
    font-size: 1rem;
    border-radius: 2px;
   
}

#what-are-we
{
    width: 100%;
}

#what-are-we p{
    width: 50%;
    margin: 0 auto;
    font-size: 1.2rem;
    font-style: italic;
}

.square-info{
    width: 40%;
    padding: 1rem;
    border-right: 1px solid #d9d9d9;
}

.square-info p, li{
    text-align: left;
    margin: 1rem 0;
    line-height: 2;
    font-size: 1rem;
    color: #2d2d2d;
}

#values{
    width: 90%;
    margin-bottom: 5rem;
}

#values h3{
    text-align: center;
    font-size: 2rem;
    margin: 5rem 0;
}

#values  h4{
    text-align: center;
    margin: 2rem 0;
    font-size: 1.5rem;
    font-weight: 300;
}

#values p{
    text-align: center;
    margin: 2rem 0;
}

@media only screen and (max-width: 800px){
    #what-are-we-info{
        width: 100%;
        margin-left: 0;
    }

    #what-are-we p{
        font-size: 0.9rem;
    }

    .square-info{
        width: 80%;
    }
}