.section{
    width: 300px;
    height: 300px;
    background: #000000ab;
    color: black;
    display: flex;
    align-items: flex-start;
    transition: width 1s, height 1s, background-color 1s;
    position: relative;
}

.section div{
    font-size: 1rem;
    width: 100%;
}

.section h2{
    font-size: 1.5rem;
    font-weight: 200;
    margin: 1rem;
    background: #0000004a;
    padding: 1rem;
    color: white;
}

.section:hover{
    width: 320px;
    height: 320px;
    background: rgba(0, 0, 0, 0.863);
}

.section-descriptions{
    color: white;
    width: 85%;
    margin: 1rem auto;
    text-align: right;
    font-weight: 200;
    line-height: 1.5;
    font-size: 1.2rem;
}

.transparent-cover{
    position: absolute;
    width: 100%;
    height: 100%;
}

@media only screen and (min-width: 1400px){
    .section{
        width: 500px;
        height: 500px;
    }

    .section:hover{
        width: 550px;
        height: 550px;
    }
}

@media only screen and (max-width: 800px){
    .section-descriptions{
        display: block !important;
    }
}