#footer{
    background: black;
    color: white;
    min-height: 20vh;
    display: flex;
    justify-content: flex-start;
    align-content: center;
    flex-wrap: wrap;
    padding-top: 1rem;
    padding-bottom: 1rem;
    column-gap: 1rem;
    row-gap: 1rem;
}


#footer p{
    text-align: center;
}

.info-footer{
    width: 20%;
    font-size: 0.8rem;
    margin: 1rem;
}

#copyright{
    width: 100%;
    font-size: 0.6rem;
}

.fa{
    margin: 0.1rem;
}

@media only screen and (max-width: 800px){
    .info-footer{
        width: 40%;
    }
}