
#us{
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-content: center;
    padding: 1rem 0;
    flex-wrap: wrap;
    row-gap: 1rem;
}

#us h2{
    background-color: rgba(240, 240, 240, 0.85);
    width: 80%;
    padding: 1rem;
    margin-left: 1rem;
    color: rgb(0, 77, 91);
    font-size: 1.5rem;
}

.us-title{
    align-self: flex-start;
    justify-content: flex-start;
    width: 100%;
    margin-bottom: 2rem;
}

#persons{
    display: flex;
    flex-wrap: wrap;
    width: 60%;
    justify-content: space-evenly;
    row-gap: 2rem;
    column-gap: 1rem;
    background-color: rgba(239, 239, 239, 0.23);
    padding: 2rem 0;
    margin-bottom: 1.5rem;
}

#persons img{
    width: 100%;
    height: auto;
    position: relative;
}

.relative-pos{
    position: relative;
    width: 20%;
    overflow: hidden;
    box-shadow: 10px 10px 20px black;
    border-radius: 2px;
    border: 5px solid white;
}

.relative-pos h3{
    font-size: 0.9rem;
    text-align: center;
    font-weight: 200;
    
}

.test-op{
    background: rgba(255, 255, 255, 0.8);
    position: absolute;
    z-index: 1;
    width: 100%;
    bottom: 2px;
    height: 20%;
    padding: 0.5rem 0;
    bottom: -30%;
}


@media only screen and (max-width: 800px)
{
    #persons{
        width: 100%;
        margin: 5rem 0;
        
    }

    .relative-pos{
        width: 40%;
    }
}