#brands{
    margin-top: 5rem;
    margin-bottom: 5rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 1rem;
    flex-wrap: wrap;
    row-gap: 1rem;
}

#brands img{
    width: 10%;
    height: auto;
}

#brands h2{
    width: 100%;
    text-align: center;
    font-weight: 200;
    font-size: 1rem;
    color: rgb(0, 105, 119);
}

@media only screen and (max-width: 800px){
    #brands{
        row-gap: 1.5rem;
    }

    #brands img{
        width: 50%;
        padding: 1rem 20%;
    }

    #brands h2{
        font-size: 1.5rem;
    }

}