#what-do-we-do-info{
    width: 80%;
    display: flex;
    justify-content: space-evenly;
    align-content: center;
    flex-wrap: wrap;
    column-gap: 1rem;
}

.wdwd-info-sec{
    background: rgb(158, 52, 88);
    padding: 1rem;
    margin: 5rem auto;
    width: 350px;
    height: 500px;
    clip-path: polygon(0% 0%, 100% 0%, 100% 77%, 16% 77%, 0 98%, 0 84%, 0 74%);
}

.wdwd-info-sec li{
    text-align: right;
    font-size: 1rem;
    color: white;
    margin: 0.4rem 1rem;
}

.wdwd-info-sec ul{
    list-style-type:square;
}

.background-1{
    background: rgb(0, 105, 119);
}

.background-2{
    background: rgb(204, 201, 64);
}

.background-3{
    background: rgb(158, 52, 88);
}

.background-4{
    background: rgb(126, 127, 129);
}

/* @media only screen and (max-width: 800px){

    .wdwd-info-sec li{
        font-size: 0.9rem;
        margin: 1rem 0.5rem;
    }

} */
.wdwd-info-list{
    display: none;
}

.wdwd-sec-titles{
    color: white;
    font-weight: 200;
    text-align: right;
    margin: 2rem 1rem;
    font-size: 2rem;
}

@keyframes change-opacity {
    0%   {opacity: 0;}
    25%  {opacity: 0.25;}
    50%  {opacity: 0.50;}
    100% {opacity: 1;}
}

@media only screen and (max-width: 800px) {
    #what-do-we-do-info{
        width: 100%;
    }
}
@media only screen and (min-width: 1400px){
    .wdwd-info-sec{
        width: 500px;
        height: 650px;
    }
}